/**
  * Pargraphs & links
  */
p {
  margin-top: 0;
  margin-bottom: 1em;
  font-size: 1em;
}

a {
  color: #0080ff;
  text-decoration: none;
}

a:visited {
  color: #0080ff;
}

a:focus, a:hover {
  color: #005ab3;
  text-decoration: underline;
}

/**
  * Headings & corresponding classes
  */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.6em 0 0.5em;
  font-weight: 400;
  line-height: 1.305;
  color: #333333;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

h1, .h1 {
  font-size: 2.0736em;
}

h1:not(.h1), .h1:not(.h1) {
  margin-top: 0;
}

h2, .h2 {
  font-size: 1.728em;
}

h3, .h3 {
  font-size: 1.44em;
}

h4, .h4 {
  font-size: 1.2em;
}

h5, .h5 {
  font-size: 1em;
}

small, h6, .h6 {
  font-size: 0.83333em;
}

/**
  * Blockquotes with a footer
  */
blockquote {
  border-left: 2px solid #333333;
  margin: 0 0 1em 0;
  padding: 0 1em;
}

blockquote footer {
  margin-top: 0.5em;
  font-size: 0.83333em;
  color: #737373;
}

blockquote footer:before {
  content: '\2014 \00A0';
}

/**
  * Formatting
  */
code, kbd, pre {
  font-family: "Lucida Console", Monaco, monospace;
  font-size: calc(1em * 0.82);
  border-radius: 4px;
}

code, kbd {
  padding: 0.2em 0.5em;
  display: inline;
  vertical-align: baseline;
  background: #D9D9D9;
}

kbd {
  background: #333333;
  color: white;
}

pre {
  padding: 0.7rem 1rem;
  display: block;
  background-color: #D9D9D9;
  line-height: 1.45;
  overflow: auto;
  white-space: pre;
}

pre code, pre kbd {
  font-size: inherit;
  background-color: transparent;
}

mark {
  background-color: #ffffa8;
  /* Give <mark> a slightly softer yellow */
  border-radius: 4px;
}

