/**
  * Three button styles: default, outlined and stripped.
  * All of the buttons include the standard button styles. See mixins.
  */
.button {
  display: inline-block;
  text-decoration: none !important;
  font-family: inherit;
  line-height: 1.45;
  margin-right: 1rem;
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button:active {
  transform: scale(0.97);
}

.button.h1, .button.h2, .button.h3, .button.h4, .button.h5, .button.h6 {
  padding-left: 0.9em;
  padding-right: 0.9em;
}

.button.button-default {
  background-color: #0080ff;
  color: white;
}

.button.button-default:visited {
  background-color: #0080ff;
  color: white;
}

.button.button-default:hover, .button.button-default:focus {
  background-color: #0066cc;
  color: white;
}

.button.button-default:active {
  background-color: #0061c2;
  color: white;
}

.button.button-outlined {
  background-color: transparent;
  border-color: #0080ff;
  color: #0080ff;
}

.button.button-outlined:visited {
  color: #0080ff;
}

.button.button-outlined:hover, .button.button-outlined:focus {
  color: #0066cc;
  border-color: #0066cc;
}

.button.button-outlined:active {
  color: #0061c2;
  border-color: #0061c2;
}

.button.button-stripped {
  background-color: transparent;
  color: #0080ff;
}

.button.button-stripped:visited {
  color: #0080ff;
}

.button.button-stripped:hover, .button.button-stripped:focus {
  color: #0066cc;
}

.button.button-stripped:active {
  color: #0061c2;
}

.button.button-full-width {
  display: block;
  width: 100%;
}

/**
  * Unordered lists & Ordered lists
  * These are considered typographic elements.
  * Paddings and margins set in EMs because the font size should dictate distances here.
  */
ul, ol {
  margin: 0 0 1em 1em;
  padding: 0;
  /* Extra padding for nested lists */
}

ul li, ol li {
  margin-bottom: 0.2em;
}

ul ul, ul ol, ol ul, ol ol {
  margin: 0.4em 0 0.4em 1em;
}

/**
  * Description lists
  */
dl {
  margin: 0 0 1em 0;
  padding: 0;
}

dl dt {
  margin-bottom: 0.2em;
}

dl dd {
  margin: 0 0 0.2em 1em;
}

dl dl {
  margin: 0.4em 0 0.4em 1em;
}

/**
  * Base tag
  */
form {
  margin-bottom: 1.5rem;
}

/**
  * Global label styles,
  * Checkbox, Radiobutton - block/inline - , file upload.
  * * * Checkbox & Radio's are wrapped in labels
  * * * -moz-url-prefixes added to prevent weird alignment of radio and checkboxes
  */
label {
  display: block;
  margin-bottom: 0.5rem;
}

/**
  * form elements for extra bottom margin to structure forms.
  */
.form-part {
  border: 0;
  padding: 0;
  margin: 0 0 1.5rem;
}

.checkbox, .radio {
  padding-left: 25px;
  /* fixed padding because radio's and checkboxes don't scale well */
  margin-top: 0;
  margin-bottom: 1rem;
  position: relative;
  display: table;
  cursor: pointer;
}

.checkbox input, .radio input {
  position: absolute;
  width: auto;
  margin: 0;
  left: 0;
  cursor: pointer;
}

.checkbox input[type="checkbox"], .radio input[type="checkbox"] {
  top: calc(1em / 3);
}

@-moz-document url-prefix() {
  .checkbox input[type="checkbox"], .radio input[type="checkbox"] {
    top: calc(1em / 2.6);
  }
}

.checkbox input[type="radio"], .radio input[type="radio"] {
  top: calc(1em / 4);
}

@-moz-document url-prefix() {
  .checkbox input[type="radio"], .radio input[type="radio"] {
    top: calc(1em / 2.7);
  }
}

/* inline checkbox & radio */
.checkbox-inline, .radio-inline {
  display: inline-block;
  margin-right: 1.2rem;
  margin-bottom: 0;
}

/**
  * General input styling
  */
input[type="text"],
input[type="search"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="number"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="datetime-local"],
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  display: block;
  width: 100%;
  border: 1px solid #D9D9D9;
  border-radius: 4px;
  line-height: 1.45;
  padding: 0.5rem 0.8rem;
  appearance: none;
  background-color: white;
}

input[type="text"]::placeholder,
input[type="search"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder,
input[type="tel"]::placeholder,
input[type="url"]::placeholder,
input[type="number"]::placeholder,
input[type="date"]::placeholder,
input[type="month"]::placeholder,
input[type="time"]::placeholder,
input[type="datetime-local"]::placeholder,
select::placeholder,
textarea::placeholder {
  color: #737373;
  opacity: 1;
}

input[type="text"]:focus,
input[type="search"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="datetime-local"]:focus,
select:focus,
textarea:focus {
  border-color: #0080ff;
  outline: none;
}

/**
  * Specific Password styling
  */
input[type="password"] {
  font-family: verdana;
}

input[type="password"]::placeholder {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/**
  * Specific file input styling
  */
input[type="file"] {
  line-height: 1;
  font-size: 16px;
  /* enforce for consistent behavior */
  border: 0;
  padding-left: 0;
  padding-right: 0;
}

/**
  * Specific textarea styling
  */
textarea {
  width: 100% !important;
  min-height: 10rem;
}

/**
  * Specific select styling
  * Browser do not style selects in the same way as regular single line inputs
  * so additional css is required.
  */
select {
  cursor: pointer;
}

select:not([multiple]) {
  background-image: url("data:image/png;base64,");
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: right center;
  background-origin: content-box;
  height: 3.5rem;
}

@media (min-width: 768px) {
  select:not([multiple]) {
    height: 2.87rem;
  }
}

select::-ms-expand {
  display: none;
}

@-moz-document url-prefix() {
  select {
    padding: 0.6rem 0.8rem;
  }
}

/**
  * Progress, meter and range styling
  */
progress, meter, input[type="range"] {
  width: 100%;
}

/**
  * Horizontal rule
  */
hr {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  border: 0;
  border-top: 1px solid #D9D9D9;
}

/**
  * Disabled styles for forms
  */
.disabled,
[disabled] {
  pointer-events: none;
  cursor: default;
  opacity: 0.5;
}

.disabled [disabled],
[disabled] [disabled] {
  opacity: 1;
}

/**
  * Table styling
  * * * thead, tbody, tfoot
  */
table {
  max-width: 100%;
  width: 100%;
  background-color: transparent;
  border-collapse: collapse;
  margin-bottom: 1.5rem;
}

table thead tr th {
  text-align: left;
  padding: 0.6rem;
  vertical-align: top;
  font-weight: 700;
  border-bottom: 1px solid #D9D9D9;
}

table tbody tr td, table tbody tr th {
  text-align: left;
  padding: 0.6rem;
  vertical-align: top;
  border-bottom: 1px solid #D9D9D9;
}

table tbody tr td:not(td), table tbody tr th:not(td) {
  font-weight: 700;
}

table tfoot tr td {
  font-weight: 700;
  text-align: left;
  padding: 0.6rem;
  vertical-align: top;
  border-bottom: none;
}

/**
  * Clearfix
  */
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

/**
  * Floats
  */
.float-left {
  float: left;
}

.float-right {
  float: right;
}

/**
  * Center block
  */
.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/**
  * Text align
  */
.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

/**
  * Showing and hiding / specific viewports
  */
.show {
  display: block !important;
}

.hide,
.show-pp,
.show-pl,
.show-tp,
.show-tl,
.show-dt {
  display: none !important;
}

@media (max-width: 539px) {
  .show-pp {
    display: block !important;
  }
  .hide-pp {
    display: none !important;
  }
  .text-center-pp {
    text-align: center;
  }
  .text-left-pp {
    text-align: left;
  }
  .text-right-pp {
    text-align: right;
  }
}

@media (min-width: 540px) and (max-width: 767px) {
  .show-pl {
    display: block !important;
  }
  .hide-pl {
    display: none !important;
  }
  .text-center-pl {
    text-align: center;
  }
  .text-left-pl {
    text-align: left;
  }
  .text-right-pl {
    text-align: right;
  }
}

@media (min-width: 768px) and (max-width: 990px) {
  .show-tp {
    display: block !important;
  }
  .hide-tp {
    display: none !important;
  }
  .text-center-tp {
    text-align: center;
  }
  .text-left-tp {
    text-align: left;
  }
  .text-right-tp {
    text-align: right;
  }
}

@media (min-width: 991px) and (max-width: 1199px) {
  .show-tl {
    display: block !important;
  }
  .hide-tl {
    display: none !important;
  }
  .text-center-tl {
    text-align: center;
  }
  .text-left-tl {
    text-align: left;
  }
  .text-right-tl {
    text-align: right;
  }
}

@media (min-width: 1200px) {
  .show-dt {
    display: block !important;
  }
  .hide-dt {
    display: none !important;
  }
  .text-center-dt {
    text-align: center;
  }
  .text-left-dt {
    text-align: left;
  }
  .text-right-dt {
    text-align: right;
  }
}

.show-elements {
  outline: 1px solid rgba(0, 0, 0, 0.3);
}

.show-elements * {
  outline: 1px solid rgba(0, 0, 255, 0.4);
}

.show-size {
  outline: none !important;
}

.show-size:after {
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 0.83333em;
  padding: 0.5rem;
  background-color: #D9D9D9;
  border-radius: 4px;
  display: block;
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  outline: none !important;
}

@media (max-width: 539px) {
  .show-size:after {
    content: "Phone portrait size";
  }
}

@media (min-width: 540px) and (max-width: 767px) {
  .show-size:after {
    content: "Phone landscape size";
  }
}

@media (min-width: 768px) and (max-width: 990px) {
  .show-size:after {
    content: "Tablet portrait size";
  }
}

@media (min-width: 991px) and (max-width: 1199px) {
  .show-size:after {
    content: "Tablet landscape size";
  }
}

@media (min-width: 1200px) {
  .show-size:after {
    content: "Desktop size";
  }
}
